@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    margin: 0;
    padding: 0;
    font-family: "Poppins", serif;
}

.stopwatch{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    align-items: center;
    background-color: rgb(0, 135, 172);
}

.container{
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    box-shadow: rgb(255, 255, 255) 0px 1px 3px, rgb(255, 255, 255) 0px 1px 2px;
    padding: 20px;
    align-items: center;
    width: 40%;
    background-color: black;
    border-radius: 8px;
}

.heading h1{
    color: white;
    font-weight: 400;
    margin-bottom: 30px;
}

.timer p{
    color: white;
    text-align: center;
    font-size: 30px;
   letter-spacing: 5px;
   margin-bottom: 30px;
}

.buttons button{
    padding: 10px;
    width: 70px;
    margin-left: 6px;
    background-color: rgb(41, 184, 224);
    border: none;
    border-radius: 5px;
    color: white;
    transition: 0.5s;
}

.buttons button:hover{
    background-color: rgb(0, 135, 172);
}
.laps{
    display: flex;
    flex-direction: column;
}

.laps h2{
    color: white;
    margin-top: 20px;
}

.laps ul{
    display: flex;
    flex-direction: column;
    color: white;
    list-style-type: none;
}

.laps ul li{
    margin-top: 5px;
    padding: 10px;
    color: aqua;
    list-style-type:decimal;
    border-radius: 6px;
}

.laps ul li:hover{
    cursor: pointer;
}
span{
    color: aqua;
}

@media screen and (max-width:800px){
    .container{
        width: 90%;
    }
}